<template>
    <div class="passwdchange">
        <!-- app title -->
        <navigation-bar
                class="task-nav-bar"
                pageName="忘记密码"
                :border="false"
                @onLeftClick="goBack"
        />
        <div class="passwd_content">
            <div class="loginHeader"></div>
            <div class="loginTitle">忘记密码</div>
            <div class="loginArea">
                <div class="phoneArea">
                    <input class="phoneInput" onkeyup="value=value.replace(/[^\d]/g,'')" maxlength="11"
                           type="text"
                           placeholder="手机号" v-model="phoneNum">
                </div>
                <div class="checkArea">
                    <div class="check_code_div">
                        <input type="text" class="checkInput" placeholder="验证码" v-model="phoneCode">
                        <div class="checkButton" @click="sendPhoneCode($event)">&nbsp;&nbsp;{{verifyCodeStr}}&nbsp;&nbsp;
                        </div>
                    </div>
                </div>
                <div class="phoneArea">
                    <input class="phoneInput" maxlength="11"
                           type="password"
                           placeholder="输入新密码" v-model="newUserPassWd">
                </div>
                <div class="phoneArea">
                    <input class="phoneInput" maxlength="11"
                           type="password"
                           placeholder="确认密码" v-model="newUserPassWd2">
                </div>
            </div>
            <div class='loginBut' @click="updateNewPassword" plain>确认修改</div>
        </div>
    </div>
</template>

<script>
import NavigationBar from "../title/NavigationBar";
import {
  getPhoneCode,
  getPropertyUser,
  getNowDate,
  changePassword,
  getRsaPublicKey
} from "../../services/loginService.js";
import log from "../../utils/log.js";

export default {
  name: "PasswdChange",
  components: { NavigationBar },
  data() {
    return {
      containerName: "PasswdChange",
      verifyCodeStr: "发送验证码",
      phoneNum: "",
      phoneCode: "",
      publicKey: "", //公钥
      rsaPassWord:"",
      newUserPassWd: "",
      newUserPassWd2: "",
      time1: 0,
      id: "0",
      clockID: ""
    };
  },
  activated() {
    this.resetData();
  },
  methods: {
    // get verify code
    resetData() {
      this.phoneNum = "";
      this.phoneCode = "";
      this.rsaPassWord = "";
      this.newUserPassWd = "";
      this.newUserPassWd2 = "";
      this.verifyCodeStr = "发送验证码";
      window.clearInterval(this.clockID);
      this.clockID = "";
      this.id = "0";
      this.getPubKey(false);
    },
    sendPhoneCode: function(event) {
      if (undefined === this.phoneNum || "" === this.phoneNum) {
        this.showMsg("手机号不能为空");
        return;
      } else if (
        !/^(((11[0-9]{1})|(12[0-9]{1})|(13[0-9]{1})|(14[0-9]{1})|(15[0-9]{1})|(16[0-9]{1})|(17[0-9]{1})|(18[0-9]{1})|(19[0-9]{1}))+\d{8})$/.test(
          this.phoneNum
        )
      ) {
        this.showMsg("请输入正确的手机号");
        return;
      }

      this.time1 = getNowDate();
      var verifyCodeRequestData = {
        PHONE: this.phoneNum
      };
      if (this.id === "0") {
        if (!this.publicKey) {
          console.log("key " + JSON.stringify(this.publicKey));
          this.getPubKey(false, true);
          return;
        }
        let rsaInfo = this.phoneNum + ',' + '10';
        this.rsaPhoneInfo = this.$getRSASign(rsaInfo, this.publicKey);
        log.i(this.rsaPhoneInfo);
        let accntInfo = this.rsaPhoneInfo;
        getPhoneCode(accntInfo)
          .then(result => {
            console.log(JSON.stringify(result));
            if (result.resultCode == 0) {
                    this.id = "1";
                    try {
                      var totalTime = 60;
                      this.verifyCodeStr = totalTime + "秒后重发";
                      this.clockID = window.setInterval(() => {
                        totalTime--;
                        if (totalTime > 0) {
                          this.verifyCodeStr = totalTime + "秒后重发";
                        }
                        if (totalTime <= 0) {
                          window.clearInterval(this.clockID);
                          this.verifyCodeStr = "重发验证码";
                          totalTime = 60;
                          this.id = "0";
                        }
                      }, 1000);
                    } catch (e) {
                      this.showMsg(e);
                    }
            } else {
              this.showMsg(result.msg);
            }
          })
          .catch(error => {
            console.log("get phone code failed." + error);
            this.showMsg(error);
          })
        // var userData = {
        //   PHONE: this.phoneNum
        // };
        // getPropertyUser(userData)
        //   .then(result => {
        //     if ("1" === result.success) {
        //       // get verify code
        //       getPhoneCode(verifyCodeRequestData)
        //         .then(result => {
        //           var returnData = result;
        //           if (undefined == returnData) {
        //             return;
        //           }
        //           if (true == returnData.success) {
        //             this.id = "1";
        //             try {
        //               var totalTime = 60;
        //               this.verifyCodeStr = totalTime + "秒后重发";
        //               this.clockID = window.setInterval(() => {
        //                 totalTime--;
        //                 if (totalTime > 0) {
        //                   this.verifyCodeStr = totalTime + "秒后重发";
        //                 }
        //                 if (totalTime <= 0) {
        //                   window.clearInterval(this.clockID);
        //                   this.verifyCodeStr = "重发验证码";
        //                   totalTime = 60;
        //                   this.id = "0";
        //                 }
        //               }, 1000);
        //             } catch (e) {
        //               this.showMsg(e);
        //             }
        //           } else {
        //             this.showMsg(result.msg);
        //           }
        //         })
        //         .catch(error => {
        //           this.showMsg(error);
        //         });
        //     } else {
        //       this.showMsg(result.msg);
        //     }
        //   })
        //   .catch(error => {
        //     this.showMsg(error);
        //   });
      } else {
        this.showMsg("请稍后重试！");
      }
    },
    showMsg: function(msg) {
      this.$toast(msg);
    },
    updateNewPassword: function() {
      if (!this.publicKey) {
        console.log("key " + JSON.stringify(this.publicKey));
        this.getPubKey(true);
        return;
      }
      var loginData = {
        USER_ACCNT: "",
        USER_PWD: ""
      };

      if (undefined == this.phoneNum || "" == this.phoneNum) {
        this.showMsg("手机号不能为空");
        return;
      } else if(undefined == this.phoneCode || "" == this.phoneCode) {
        this.showMsg("验证码不能为空");
        return;
      }else if (undefined == this.newUserPassWd || "" == this.newUserPassWd) {
        this.showMsg("密码不能为空");
        return;
      } else if (
        undefined == this.newUserPassWd2 ||
        "" == this.newUserPassWd2
      ) {
        this.showMsg("两次密码输入不一致");
        return;
      } else if (this.newUserPassWd != this.newUserPassWd2) {
        this.showMsg("两次密码输入不一致");
        return;
      } else if (this.newUserPassWd.length < 6) {
        this.showMsg("密码不能少于6位");
        return;
      } else if (this.newUserPassWd.length > 16) {
        this.showMsg("密码不能多于16位");
        return;
      } else if (this.time1 === 0) {
        this.showMsg("请重新获取验证码");
        return;
      }
      var time2 = getNowDate(); /*点击登录操作的时间*/
      var date1 = new Date(this.time1.replace(/\-/g, "/")); /*获取验证码时间*/
      var date2 = new Date(time2.replace(/\-/g, "/"));
      var date3 = date2.getTime() - date1.getTime();
      //计算出相差天数
      // var days = Math.floor(date3 / (24 * 3600 * 1000));
      //计算出小时数
      // var leave1 = date3 % (24 * 3600 * 1000);    //计算天数后剩余的毫秒数
      // var hours = Math.floor(leave1 / (3600 * 1000));/*小时数*/
      //计算相差分钟数
      // var leave2 = leave1 % (3600 * 1000);        //计算小时数后剩余的毫秒数
      // var minutes = Math.floor(leave2 / (60 * 1000));
      //计算相差秒数
      // var leave3 = leave2 % (60 * 1000)      //计算分钟数后剩余的毫秒数
      var seconds = Math.round(date3 / 1000);
      log.l(
        "heijianye- date1: " +
          date1 +
          "; date2: " +
          date2 +
          "; date3: " +
          date3 +
          "; seconds: " +
          seconds
      );
      if (300 < seconds) {
        this.showMsg("请重新发送验证码");
      } else {
        /*大于300秒可以修改*/
        // loginData.USER_ACCNT = this.phoneNum;
        // loginData.USER_PWD = this.newUserPassWd;
        // loginData.PHONE = this.phoneNum;
        // loginData.VERIFICATION = this.phoneCode;
        let baseNewPassWord = btoa(this.newUserPassWd);
        console.log(baseNewPassWord);
        let rsaInfo = this.phoneNum+',' + this.phoneCode + ',' + baseNewPassWord;
        this.rsaPassWord = this.$getRSASign(rsaInfo, this.publicKey);
        log.i(this.rsaPassWord);
        let modifyInfo = this.rsaPassWord;
        changePassword(modifyInfo).then(res => {
          if ("0" == res.resultCode) {
            this.showMsg("修改成功");
            this.$router.back();
          } else {
            // this.showMsg(res.msg)
            this.showMsg(res.msg);
            log.i(res.msg);
          }
        }).catch(error => {
          console.log("passwd update failed for " + error);
          if ("网络异常！" == error) {
            this.showMsg("网络异常，请检查网络！");
          } else {
            this.showMsg("登录密码修改失败~");
          }
        });
      }
    },
    goBack: function() {
      this.$router.back();
    },
    getPubKey(forPassWord, forPhoneCode) {
      getRsaPublicKey()
          .then(res => {
            this.publicKey = res.data.publicKey;
            if (forPassWord) {
              console.log("get pub key for passwd update.");
              this.updateNewPassword();
            }
            if (forPhoneCode) {
              console.log("get pub key for phone code.");
              this.sendPhoneCode();
            }
          })
          .catch(error => {
            // this.showMsg(error);
            console.log("get pub key failed " + error);
            if (forPassWord) {
              console.log("get pub key for passwd update failed." + error);
              if ("网络异常！" == error) {
                this.showMsg("网络异常，请检查网络！");
              } else {
                this.showMsg("登录密码修改失败~");
              }
            }
            if (forPhoneCode) {
              console.log("get pub key for phone code failed." + error);
              if ("网络异常！" == error) {
                this.showMsg("网络异常，请检查网络！");
              } else {
                this.showMsg("验证码获取失败~");
              }
            }
          });
    }
  }
};
</script>

